@custom-media --wide-vp (width >= 1200px);
@custom-media --not-wide-vp (width <= 1199px);
@custom-media --middle-vp (768px <= width <= 1199px);
@custom-media --small-vp (width <= 767px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --x2 (min-resolution: 2dppx);
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/roboto-condensed-light.woff2") format("woff2"), url("../fonts/roboto-condensed-light.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-condensed.woff2") format("woff2"), url("../fonts/roboto-condensed.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/bebas-neue-regular.woff2") format("woff2"), url("../fonts/bebas-neue-regular.woff") format("woff");
}
@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/bebas-neue-bold.woff2") format("woff2"), url("../fonts/bebas-neue-bold.woff") format("woff");
}
:root {
  --accent-font: "Bebas Neue", "Impact", sans-serif;
  --regular-font: "Roboto Condensed", "Arial", sans-serif;
}

:root {
  --color-base-white: #ffffff;
  --color-base-black: #000000;
  --color-bg-img-screen: #000328;
  --color-bg-img-trainers: #ccd0de;
  --color-bg-info-trainers: rgba(16, 38, 84, 0.89);
  --color-title: #102654;
  --color-accent-text-trainers: #b6bdcc;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.page {
  height: 100%;
  font-family: var(--regular-font);
  font-weight: 400;
  font-style: normal;
  color: var(--color-base-white);
  scroll-behavior: smooth;
}

.page h1 {
  margin-top: 0;
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 700;
  font-size: 109px;
  line-height: 109px;
  letter-spacing: 0.03em;
  text-align: right;
  text-transform: uppercase;
  color: var(--color-base-white);
}

.page h2 {
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 700;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  color: var(--color-title);
  text-transform: uppercase;
}
@media (--middle-vp) {
  .page h2 {
    font-size: 42px;
    line-height: 42px;
  }
}
@media (--small-vp) {
  .page h2 {
    font-size: 32px;
    line-height: 32px;
  }
}

.page ul {
  list-style-type: none;
  padding-left: 0;
}

.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-base-white);
}

.page__visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (--wide-vp) {
  .page__wrapper-center {
    max-width: 1250px;
    padding: 0 45px;
    margin: 0 auto;
  }
}
@media (--middle-vp) {
  .page__wrapper-center {
    padding: 0 45px;
  }
}
@media (--small-vp) {
  .page__wrapper-center {
    padding: 0 15px;
  }
}

.page-main {
  flex-grow: 1;
}

.button {
  font-family: var(--regular-font);
  font-weight: 700;
  color: var(--color-base-white);
  user-select: none;
  text-transform: uppercase;
}

.page-header {
  display: flex;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}

.page-header__screen-image {
  display: block;
  width: 100%;
  max-width: 500px;
  height: 556px;
  background-color: var(--color-bg-img-screen);
}

.page-footer {
  display: flex;
}

.trainers h3,
.trainers b {
  font-size: 18px;
  line-height: 150%;
}

.trainers i {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--color-accent-text-trainers);
}

.trainers p {
  font-size: 14px;
  line-height: 150%;
}

/*.swiper-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  gap: 40px;
}*/
.trainers__slide {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  max-width: 260px;
  min-height: 294px;
  /*margin-right: 40px;*/
}

.trainers__slide-image {
  background-color: var(--color-bg-img-trainers);
  grid-column: 1/-1;
  grid-row: 1/-1;
}

.trainers__slide-title {
  margin-top: auto;
  grid-column: 1/-1;
  grid-row: 1/-1;
}

.trainers__slide-info {
  background-color: var(--color-bg-info-trainers);
  grid-column: 1/-1;
  grid-row: 1/-1;
  z-index: -1;
}

.trainers__slide:hover .trainers__slide-info,
.trainers__slide:active .trainers__slide-info {
  z-index: 1;
  cursor: pointer;
}

.trainers__slide:hover .trainers__slide-title,
.trainers__slide:active .trainers__slide-title {
  display: none;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  color: #afafaf;
  width: 16px;
  height: 32px;
}